<template>
    <div class="pieCharts">
        <div id="pieCharts" ref="pieCharts"></div>
    </div>
</template>

<script>
  import * as echarts from 'echarts';
  //这是绘制图表的参数定义
  let optionTemp =
    {
      tooltip: {
        show:false,
        trigger: 'item',
        formatter: "{a} <br/>{b} : {c} ({d}%)"
      },
      legend: {
        show:false,
        top: 'bottom',
      },
      series: [
        {
          name: '',
          type: 'pie',
          radius: ['0%', '90%'],
          center: ['50%', '50%'],
          roseType: 'radius',
          label : {
            normal : {
              // formatter: '{b}:{c}<br/> ({d}%)',
              formatter: function(params) {
                if(params.name)
                return  `{a|${params.name}} \n ${params.value}  占比${params.percent}%`
              },
              rich:{
                a:{
                  color:"#fff",
                  fontSize:"14",
                  fontWeight:"700",
                  lineHeight:"20"
                }
              },
              textStyle : {
                fontWeight : '800',
                lineHeight:14,
                fontSize:13,
                color:'#00F8FF'
              }
            }
          },
          itemStyle: {
            normal: {
              // borderRadius: 20,
              color: function(params) {
                let colorList = [{
                  c1: '#248CEB', //实践
                  c2: '#24CDEB'
                }, {
                  c1: '#24E1EB', //操作
                  c2: '#62E7E2'
                }, {
                  c1: '#52E4E0', //操作
                  c2: '#52E4E0'
                },{
                  c1: '#248CEB', //实践
                  c2: '#24CDEB'
                }]
                return new echarts.graphic.LinearGradient(1, 0, 0, 0, [{ //颜色渐变函数 前四个参数分别表示四个位置依次为左、下、右、上

                  offset: 0,
                  color: colorList[params.dataIndex].c1
                }, {
                  offset: 1,
                  color: colorList[params.dataIndex].c2
                }])
              }
            }
          },
          data: [
            {value: 6, name: '重大'},
            {value: 20, name: '较大'},
            {value: 686, name: '一般'},
            {value: 903, name: '轻微'},
          ]
        }
      ]
    }


  export default {
    name: "pieCharts",
    props: {
      index:{
        type:Number,
        default:()=>{
          return null
        }
      },
      xData:{
        /*** * xData:['一月',二月]*/
        type:Array,
        default:()=>{
          return []
        }
      },
      seriesData: {
        /*** * seriesData:[{
          name: 'legend1',
          type: 'bar',
          data: [20,10,30,60,70],
          itemStyle: {
            normal: {
              color: new echarts.graphic.LinearGradient(0, 1, 0, 0, [{
                offset: 0,
                color: "#1268f3" // 0% 处的颜色
              }, {
                offset: 0.6,
                color: "#08a4fa" // 60% 处的颜色
              }, {
                offset: 1,
                color: "#01ccfe" // 100% 处的颜色
              }], false)
            }
          },
        },]*/
        type: Array,
        default: () => {
          return []
        }
      },
      //  决定x轴和y轴显示(单位)
      _zb: {
        /** :['单位','']*/
        type: Array,
        default: () => {
          return ['单位', '']
        }
      },

    },
    watch: {

    },
    data() {
      return {
        option: optionTemp,
      }


    },
    mounted() {
      this.init();
    },
    methods: {
      dataSet(){
        this.option.yAxis[0].name = this._zb[0]
        this.option.xAxis[0].name = this._zb[1]
        this.option.xAxis[0].data = this.xData
        if (this.seriesData.length>0){
          this.option.legend.data = []
          this.seriesData.forEach(item=>{
            this.option.legend.data.push(item.name)
          })
          this.option.series = this.seriesData
        }
      },
      init() {
        // this.dataSet()
        let mychart = echarts.init(this.$refs.pieCharts, );
        window.addEventListener("resize", mychart.resize);
        //将会绘制图表的参数加到图表中

        mychart.setOption(this.option);
        let $this = this;
        mychart.on('click', function (params) {
          $this.$emit('getData',params,$this.index)
          // window.console.log(params);
        });

      },
    }

  }
</script>

<style scoped lang="scss">
    .pieCharts {
        height: 100%;
        #pieCharts {
            height: 100%;
            /*width: 97%;*/
            /*height: 99%;*/
        }
    }
</style>